






















import segmentedControl from '../../../components/segmented-control.vue';
export default {
	data() {
		return {
			tabitems: [
				'每日统计',
				'每月统计',
				'每年统计'
			],
			current: 0,
			activeColor: '#17393c',
			styleType: 'text'
		}
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	},
	components: {
		segmentedControl
	}
}



